<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      :right-text="flag?'登录':'注册'"
      left-arrow
      @click-left="gotoBack"
      @click-right="gotoLogin"
    />
  </div>
</template>

<script>
export default {
    props:{
        title:{
            type:String,
            default:"xxxx"
        },
        flag:{
            type:Boolean,
            default:true
        }
    },
    methods: {
    gotoBack() {
       this.$router.go(-1)
    },
    gotoLogin() {
        this.$router.push({name:this.flag?"login":"register"})
    }
  }
};
</script>

