<template>
  <div>
    <Rlhead id title="注册" :flag="true"></Rlhead>
    <div class="login-box">
      <van-cell-group class="input-name">
        <van-field
          v-model="register.username"
          clearable
          label="用户名"
          right-icon="question-o"
          placeholder="请输入用户名"
          @click-right-icon="$toast('请输入2-6位字母或数字组成的用户名')"
          class="name-box"
          style="background:rgba(0,0,0,0.1)"
          :flag-name="flag.name"
          @blur="pdName"
        />
      </van-cell-group>
      <van-cell-group class="input-pwd">
        <van-field
          v-model="register.tel"
          label="手机号"
          right-icon="question-o"
          placeholder="请输入手机号"
          @click-right-icon="$toast('请输入11位数字的手机号')"
          class="tel-box"
          style="background:rgba(0,0,0,0.1)"
          :flag-tel="flag.tel"
          @blur="pdTel"
        />
      </van-cell-group>
      <van-cell-group class="input-pwd">
        <van-field
          v-model="register.password"
          type="password"
          right-icon="question-o"
          label="密码"
          placeholder="请输入密码"
          @click-right-icon="$toast('请输入6-12位字母或数字组成的密码')"
          class="pwd-box"
          style="background:rgba(0,0,0,0.1)"
          :flag-pwd="flag.pwd"
          @blur="pdPwd"
        />
      </van-cell-group>
      <van-cell-group class="input-pwd">
        <van-field
          v-model="register.dbpwd"
          type="password"
          label="确认密码"
          right-icon="question-o"
          placeholder="请确认密码"
          :flag-dbpwd="flag.dbpwd"
          @click-right-icon="$toast('请确认密码-')"
          @blur="pdDbPwd"
          class="dbpwd-box"
          style="background:rgba(0,0,0,0.1)"
        />
      </van-cell-group>
      <van-button round type="primary" size="large" @click="todoRegister">注 册</van-button>
    </div>
  </div>
</template>

<script>
import Rlhead from "@/components/Rlhead.vue";
export default {
  data() {
    return {
      register: {},
      flag: {
        name: false,
        tel: false,
        pwd: false,
        dbpwd: false
      }
    };
  },
  components: {
    Rlhead: Rlhead
  },
  methods: {
    todoRegister() {
      let count = 0;
      for (let item in this.flag) {
        if (this.flag[item]) {
          count++;
        }else{
          // this.$toast("请输入正确的用户名");
          if(item == "name"){
          this.$toast("请输入正确的用户名");
          return false
          }
          if(item == "tel"){
          this.$toast("请输入正确的手机号");
          return false
          }
          if(item == "pwd"){
          this.$toast("请输入正确密码");
          return false

          }
          if(item == "dbpwd"){
          this.$toast("两次密码不相同");
          return false

          }
        }
      }
      if (count == 4) {
        this.$axios.post("/vue/register", this.register).then(res => {
          if (!!res.data.type) {
            this.$router.push({ name: "login" });
          } else {
            console.log("注册失败");
          }
          this.register = {};
        });
      }
    },
    pdName() {
      // console.log(this.register.username)
      let username = this.register.username;
      const reg = /^[0-9a-zA-Z]{2,6}$/;
      // if()
      if (!reg.test(username)) {
        this.$toast("请输入正确的用户名");
      } else {
        this.flag.name = true;
      }
    },
    pdTel() {
      let tel = this.register.tel;
      const reg = /^1\d{10}$/;
      if (!reg.test(tel)) {
        this.$toast("请输入正确的手机号");
      } else {
        this.flag.tel = true;
      }
    },
    pdPwd() {
      let pwd = this.register.password;
      const reg = /^[0-9a-zA-Z]{6,12}$/;
      if (!reg.test(pwd)) {
        this.$toast("请输入正确的密码");
      } else {
        this.flag.pwd = true;
      }
    },
    pdDbPwd() {
      let pwd = this.register.password;
      let dbpwd = this.register.dbpwd;
      if (pwd === dbpwd) {
        this.flag.dbpwd = true;  
      } else {
        this.$toast("两次密码不相同");
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.login-box {
  padding: 0 0.5rem;
  margin-top: 3rem;
}
.input-name,
.input-pwd {
  margin-bottom: 0.5rem;
  border-radius: 0.5rem;
  text-align: center;
}
.pwd-box,
.name-box,
.tel-box,
.dbpwd-box {
  border-radius: 0.5rem;
}
</style>
